import { createAsyncThunk, createSlice } from '@reduxjs/toolkit';

import { RootState } from '.';
import { API } from '../constants';
import axios from 'axios';
export interface ContactObjectState {
  name?: string;
  company?: string;
  title?: string;
  officeNumber?: string;
  mobileNumber?: string;
  email?: string;
  officeAddress?: string;
  notes?: string;
  designation?: string;
  branchName?: string;
}
export interface CardDataObjectState {
  key?: string;
  label?: string;
  value?: string;
}
export interface ContactState {
  ebizData: any;
  loading: boolean;
  error: boolean;
  errorStatus: any
}

export const fetchEbizData = createAsyncThunk(
  'fetchEbizData',
  async (request: string, { getState, rejectWithValue }) => {
    try {
      if (request === undefined || request === null) {
        return rejectWithValue('Failed to fetch data');
      } else {
        const result = await axios.get(API + `/EBiz/web/${request}`);
        console.log('res1', result);
        return result;
      }
    } catch (error) {
      console.log('error1', error);
      return rejectWithValue(error.message || 'Failed to fetch data');
    }
  }
);

const initialState: ContactState = {
  ebizData: [],
  loading: true,
  error: false,
  errorStatus: null
};

export const ContactSlice = createSlice({
  name: 'contact',
  initialState,
  reducers: {
    setEbizData: (state, { payload }) => {
      state.ebizData = payload
    },
    setLoading: (state, { payload }) => {
      state.loading = payload
    },
  },
  extraReducers: (builder) => {
    builder.addCase(fetchEbizData.rejected, (state, { payload }) => {
      state.loading = false;
      state.ebizData = [
        {
          key: 'name',
          label: 'Name',
          isVisible: true,
          value: 'Jonathan Chong Wan Lin',
          isEditable: false,
          isRequired: false,
        },
        {
          key: 'company',
          label: 'Company',
          isVisible: true,
          value: 'Public Bank Berhad',
          isEditable: true,
          isRequired: false,
        },
        {
          key: 'designation',
          label: 'Designation',
          value: 'Senior Financial Executive',
          isVisible: true,
          isEditable: false,
          isRequired: false,
        },
        {
          key: 'branchTel',
          label: 'Office Number(s)',
          isVisible: true,
          value: '03-2176 7888, 03-2163 8866',
          isEditable: true,
          isRequired: false,
        },
        {
          key: 'branchFax',
          label: 'Fax Number(s)',
          isVisible: true,
          value: '03-2163 9901, 03-2163 9932',
          isEditable: true,
          isRequired: true,
        },
        {
          key: 'mobileNumber',
          label: 'Mobile Number',
          isVisible: true,
          value: '012-345 6789',
          isEditable: true,
          isRequired: true,
        },
        {
          key: 'email',
          label: 'Email',
          isVisible: true,
          value: 'jonathan.chong@publicbank.com.my',
          isEditable: true,
          isRequired: true,
        },
        {
          key: 'branchName',
          label: 'Location',
          isVisible: true,
          value: 'KL City Main Office',
          isEditable: true,
          isRequired: false,
        },
        {
          key: 'branchAddress',
          label: 'Office Address',
          isVisible: true,
          value:
            'Ground Floor, Menara Public Bank, 146, Jalan Ampang, 50450 Kuala Lumpur',
          isEditable: true,
          isRequired: false,
        },
      ];
      console.log('payload', payload);

      // state.error = true;
      // state.errorStatus = payload
    });
    builder.addCase(fetchEbizData.pending, (state, { payload }) => {
      state.loading = true;
    });
    builder.addCase(fetchEbizData.fulfilled, (state, { payload }) => {
      state.loading = false;
      console.log('payload', payload);
      state.ebizData = payload.data;
    });
  },
});

export const { setEbizData, setLoading } = ContactSlice.actions;

export const eBizData = (state: RootState) => state.contact.ebizData;
export const eBizDataLoading = (state: RootState) => state.contact.loading;
export const eBizDataError = (state: RootState) => state.contact.error;
export const eBizDataErrorStatus = (state: RootState) => state.contact.errorStatus;

export default ContactSlice.reducer;
